
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: 'orderedGoods',
components: {
XTextarea
},
data () {
return {


detail: {
checkInDate: '2019-01-02', // 入住时间
couponInfo: [], // 优惠信息
customProjectInfo: [], // 定制项目订单信息 ,
leaveDate: '2019-01-03', // 离店时间 ,
leaveMessage: '我是留言', // 留言信息
orderAmt: 50, // 订单金额
orderId: '', // 订单号
orderStatus: 1,
refundFailReason: '', // 退款失败理由
refundReason: '', // 退款理由
roomInfo: [], // 房源订单信息
scheduleInfo: [], // 订单流程进度信息
travellerInfo: [], // 入住人信息
userName: '小白', // 预定人姓名 ,
userPhone: '15700353375'
},

couponInfo: [
{
amount: 99.00,
couponType: 0, // 优惠券类型【0-商家券 1-平台券】 ,
title: '满200减20'
},
{
amount: 99.00,
couponType: 0, // 优惠券类型【0-商家券 1-平台券】 ,
title: '满200减20'
},
{
amount: 99.00,
couponType: 0, // 优惠券类型【0-商家券 1-平台券】 ,
title: '满200减20'
}
],
scheduleInfo: [
{
completeTime: '2019-01-15 15:30',
tradeDes: '下单时间'
},
{
completeTime: '2019-01-15 15:30',
tradeDes: '付款时间'
},
{
completeTime: '2019-01-15 15:30',
tradeDes: '发货时间'
}
],
travellerInfo: [
{
idCard: '64165416541651461',
name: '张三'
},
{
idCard: '64165416541651461',
name: '张三'
}
],
customProjectInfo: [
{
amount: 99.00,
number: 2,
title: '我是标题',
url: '/static/img/test.7f2f298.png'
},
{
amount: 99.00,
number: 2,
title: '我是标题',
url: '/static/img/test.7f2f298.png'
}
],
roomInfo: [
{
amount: 99.00,
liveDay: 2,
title: '我是标题',
url: '/static/img/test.7f2f298.png'
}
]
}
},
created () {
this.$vux.loading.show({
text: '加载中...'
})
this.detail.travellerInfo = this.travellerInfo
this.detail.couponInfo = this.couponInfo
this.detail.scheduleInfo = this.scheduleInfo
this.detail.customProjectInfo = this.customProjectInfo
this.detail.roomInfo = this.roomInfo
this.getDetail()
},
methods: {
getBack () {
this.$router.go(-1)
},
oderSend () {
this.$router.replace({
name: 'orderSend',
query: {
orderId: this.detail.orderId,
receiverPhone: this.detail.receiverPhone,
senderPhone: this.detail.senderPhone
}
})
},

refused () {
if (!this.detail.refundReason) {
this.$vux.toast.show({
text: '拒绝理由为必填',
position: 'middle',
type: 'warn'
})
return
}
this.postData(4)
},
postData (flag) {
let params = {
amount: 0, // 订单金额【修改价格时必输】 ,
operate: flag, // 操作【0-取消订单,1-确认订单,2-办理入住,3-同意退款,4-拒绝退款,5-修改价格】 ,
orderId: this.$route.query.id, // 订单编号 ,
refuseReason: this.detail.refundReason 
}
this.$http.fetchPost('/merchant/order/update/room', params).then((res) => {
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.getBack()
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
},
getDetail () {
this.$http.fetchGet('/merchant/order/get/room/detail', {orderId: this.$route.query.id}).then((res) => {
setTimeout(() => {
this.$vux.loading.hide()
}, 500)
this.detail = res.data.data
})
},
dealStatus (status) {

let name = ''
switch (status) {
case 0: name = '待支付'; break
case 1: name = '待确认'; break
case 2: name = '申请退款'; break
case 3: name = '退款失败'; break
case 4: name = '退款成功'; break
case 5: name = '取消订单'; break
case 6: name = '待点评'; break
case 7: name = '待入住'; break
case 8: name = '入住中'; break
case 9: name = '已完成'; break
case 99: name = '所有状态'; break
default: name = '所有状态'; break
}
return name
}
}
}



